<template>
    <div class="login-wrap">
        <div class="ms-login">
            <div class="ms-title">时空商用版后台管理系统</div>
            <el-form :model="param" :rules="rules" ref="login" label-width="0px" class="ms-content">
                <el-form-item prop="name">
                    <el-input v-model="param.name" placeholder="name">
                        <template #prepend>
                            <el-button icon="el-icon-user"></el-button>
                        </template>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input
                        type="password"
                        placeholder="password"
                        v-model="param.password"
                        @keyup.enter="submitForm()"
                    >
                        <template #prepend>
                            <el-button icon="el-icon-lock"></el-button>
                        </template>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </div>
                <p class="login-tips">Tips : 用户名和密码随便填。</p>
            </el-form>
        </div>
    </div>
</template>

<script>
import httpUser from '@/api/user'
export default {
    data() {
        return {
            param: {
                name: "",
                password: ""
            },
            rules: {
                name: [
                    { required: true, message: "请输入用户名", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "请输入密码", trigger: "blur" }
                ]
            }
        };
    },
    created() {
        this.$store.commit("clearTags");
    },
    methods: {
        submitForm() {
            let userInfo = this.param
            console.log(userInfo)
            httpUser.login(userInfo).then(res => {
                console.log(res.data.roomManager)
                if(res.code === 20000){
                    let obj = JSON.stringify(res.data.roomManager)
                    window.localStorage.setItem("user", obj)
                    this.$router.push("/")
                }else {
                    window.alert("登录失败")
                    return false
                }

            })
        }
    }
};
</script>

